export default {
	data() {
		return {
			
		}
	},
	methods: {
		opennew:function(id){
			uni.navigateTo({
				url: '../'+id+'/'+id
			})
		},
		share: function() {
			uni.share({
				provider: 'weixin',
				scene: 'WXSceneSession',
				type: 1,
				summary: '我正在久万久千',
				success: function(res) {
					console.log('success:' + JSON.stringify(res));
				},
				fail: function(err) {
					console.log('fail:' + JSON.stringify(err));
				}
			});
		}
	}
}
